<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-lg-2 col-md-1"></div>
            <div class="col-lg-4 col-md-5">
                <h3>pager 1</h3>
                <button class="btn btn-md btn-primary" @click="loadPageFromServer">
                    Load Page of 200 Contacts
                </button>
                <button :class="pager1.records.length>0?'active':'disabled'" class="btn btn-md btn-primary" @click="clearContacts('pager1')">
                    Clear Contacts
                </button>
                <img v-if="spinner_pager1" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <br><br>
                <span>contacts in memory = <b>{{ pager1.records.length }}</b></span>
                <span style="margin-left: 20px;position: relative;">mode = <b>{{ pager1.mode ? pager1.mode : "std" }}</b></span>
                <!-- <span style="margin-left: 20px;">contacts totalRecords = <b>{{ pager.totalRecords }}</b></span>
                <span style="margin-left: 20px;">recordsPage length = <b>{{ pager.recordsPage.length }}</b></span> -->
                <!-- <span style="margin-left: 20px;">pagerId = <b>{{ pager.id}}</b></span> -->
                <!-- <span style="margin-left: 20px;position: absolute;" v-if="spinner_pager1">hitting server...</span> -->
                <br>
                <data-pager v-bind="pager1" @controller="pagerController"></data-pager>
                <contact-list :contacts="pager1.recordsPage"></contact-list>
                <hr>
                <h3>pager 3</h3>
                <img v-if="spinner_pager3" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <!-- <br><br> -->
                <span>contacts in memory = <b>{{ pager3.records.length }}</b></span>
                <span style="margin-left: 20px;position: relative;">mode = <b>{{ pager3.mode ? pager3.mode : "std" }}</b></span>
                <br>
                <span style="margin-left: 00px;position: relative;">pagerStyle.use = <b>{{ pager3.pagerStyle.use }}</b></span>
                <data-pager v-bind="pager3" @controller="pagerController"></data-pager>
                <contact-list :contacts="pager3.recordsPage"></contact-list>
            </div>
            <!-- <div class="col-lg-1 col-md-auto"></div>  -->
            <div class="col-lg-4 col-md-6" style="position: relative;">
                <h3>pager 2</h3>
                <button class="btn btn-md btn-primary" @click="loadBlockFromServer">
                    Load Block of 500 Contacts
                </button>
                <button :class="pager2.records.length>0?'active':'disabled'" class="btn btn-md btn-primary" @click="clearContacts('pager2')">
                    Clear Contacts
                </button>
                <img v-if="spinner_pager2" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <br><br>
                <span>contacts in memory = <b>{{ pager2.records.length }}</b></span>
                <span style="margin-left: 20px;">mode = <b>{{ pager2.mode ? pager2.mode : "std" }}</b></span>
                <!-- <span style="margin-left: 20px;position: absolute; white-space: nowrap;" v-if="spinner_pager2">hitting server...</span> -->
                <br>
                <data-pager v-bind="pager2" @controller="pagerController"></data-pager>
                <contact-list :contacts="pager2.recordsPage"></contact-list>
                <hr>
                <h3>pager 4</h3>
                <data-pager v-bind="pager4" @controller="pagerController"></data-pager>
                <div v-for="name in pager4.recordsPage">{{name}}</div>
            </div>
            <div class="col-lg-2 col-md-auto"></div>
        </div>
    </div>
</template>

<script>
    import ContactList from './contacts/ContactList.vue';
    // import * as $ from 'jquery';
    import DataPager, { createPager, createController } from './data-pager/';
    let pager1 = createPager({ id: 'pager1', events: { pageArgsChanged: 'loadContacts' } });
    let pager2 = createPager({ id: 'pager2', events: { pageArgsChanged: 'loadContacts' } });
    let pager3 = createPager({ id: 'pager3', totalRecords: 200, pagerStyle: { use: 'semantic' }, events: { pageArgsChanged: 'loadContacts', loadOnCreated: { pageNum: 1, pageSize: 5 } } });
    let pager4 = createPager({ id: 'pager4' });
    let pagerController = createController(pager1, pager2, pager3, pager4);
    export default {
        data() {
            return {
                pager1,
                pager2,
                pager3,
                pager4,
                spinner_pager1: false,
                spinner_pager2: false,
                spinner_pager3: false
            }
        },
        methods: {
            // updatePage(recordsPage) {
            //     // console.log('pageUpdated', recordsPage)
            //     this.pager.recordsPage = recordsPage;
            // },
            pagerController,
            loadPageFromServer() {
                this.pager1.set({
                    // records:[],
                    mode: "std", // leaving out mode will default to standard mode
                    totalRecords: 200, // tell pager to load 1st page of 200 records, will use pageSize as totalRecords
                    pageSizes: [
                        { size: 3, text: "3 items", selected: true },
                        { size: 8, text: "8 items", selected: false },
                        { size: 20, text: "20 items", selected: false }
                    ],
                });
                // this.pager.totalRecords =200
                this.pagerController({ name: 'pageArgsChanged', pageNum: 1, pageSize: 3, id: "pager1" }); // load initial page
            },
            loadBlockFromServer() {
                console.log('loadBlockFromServer');
                this.pager2.set({
                    mode: "block", // tell pager to operate on in-memory block, will calculate # pages automatically
                    pageSizes: undefined, // use defaults
                });
                this.pagerController({ name: 'pageArgsChanged', pageNum: 1, pageSize: 500, id: "pager2" }); // load initial page
            },
            loadContacts({ pageNum, pageSize, id }) { // will receive pageArgs from pager's pageUpdated method if totalRecords > 0
                console.log(`loadContacts(${pageNum},${pageSize},id:${id})`);
                this['spinner_' + id] = true;
                let vm = this;
                return $.getJSON('https://randomuser.me/api/?seed=foobar&page=' + pageNum + '&results=' + pageSize)
                    .then(res => {
                        // console.log('getJSON this: ', vm);
                        // simply add row number to each contact
                        let records = res.results.map((c, i) => {
                            c.row = ((pageNum - 1) * pageSize) + i;
                            return c;
                        });
                        // vm[id].records = records; // tell pager where to find data
                        setTimeout(() => vm['spinner_' + id] = false, 300);
                        // console.log(`loadContacts(${pageNum},${pageSize})`, this.pager.records);
                        return records;
                    });
            },
            clearContacts(id) {
                console.log('clearContacts: ' + id);
                this[id].records = [];
            }
        },
        components: {
            ContactList,
            DataPager
        },
        created() {
            $.getJSON('data/names.json')
                .then(names => {
                    this.pager4.set({
                        records: names,
                        mode: "block",
                        pageSizes: [{ size: 6, text: "6 items" }]
                    });
                })
        },
        destroyed() {
            pager1.records = [];
        }
    }

</script>


