<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-lg-3"></div>
            <div class="col-lg-6">
                <br><br>
                <button class="btn btn-md btn-primary" @click="loadContacts({pageSize:500})">
                    Load Block of 500 Contacts
                </button>
                <button :class="pager.records.length>0?'active':'disabled'" class="btn btn-md btn-primary" @click="pager.records=[]">
                    Clear Contacts
                </button>
                <img v-if="showSpinner" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <data-pager v-bind="pager" @pageUpdated="page=>pager.recordsPage=page"></data-pager>
                <contact-list :contacts="pager.recordsPage"></contact-list>
            </div>
            <div class="col-lg-3"></div>
        </div>
    </div>
</template>

<script>
    import ContactList from './contacts/ContactList.vue';
    import DataPager, { createPager, createController } from './data-pager/';
    // import DataPager, { createPager } from '../../build/DataPager.bundle.js';
    let pager = createPager({
        mode: 'block'
    });
    // let controller = createController(pager);
    export default {
        components: {
            ContactList,
            DataPager
        },
        data() {
            return {
                pager,
                showSpinner: false
            }
        },
        methods: {
            // controller,
            loadContacts({ pageNum = 1, pageSize = 5, id }) {
                this.showSpinner = true;
                let vm = this;
                $.getJSON('https://randomuser.me/api/?seed=foobar&page=' + pageNum + '&results=' + pageSize)
                    .then(res => {
                        // simply add row number to each contact
                        let records = res.results.map((c, i) => {
                            c.row = ((pageNum - 1) * pageSize) + i;
                            return c;
                        });

                        setTimeout(() => vm.showSpinner = false, 300); // show spinner for at least 300ms

                        vm.pager.records = records; // update pager with new data
                    })
            },
        }
    }

</script>


