// export const createPager = function ({ id = 'pager', ...events } = { id, events: {} }) {
export const createPager = function ({ id = 'pager', events = {}, ...args } = { id, events }) {
    // console.log('createPager: ', id, events, args);
    return {
        set: function (o) {
            for (let option in o) {
                // console.log('helper.options',this) 
                if (this.hasOwnProperty(option)) {
                    // console.log(option, o[option]);
                    this[option] = o[option];
                }
                else {
                    console.error(`warn: option ${option} not member of pager options`)
                }
            }
        },
        debug: false,
        events,
        id,
        initialPage: args['initialPage'] || 1,
        mode: args['mode'] || 'std',
        pagerStyle: args['pagerStyle'] || undefined,
        pageSizes: args['pageSizes'] || undefined, // undef needed to tell Vue to use default prop
        records: [],
        recordsPage: [],
        totalRecords: args['totalRecords'] || 0,
    }
}

export const createController = function (...pagers) {
    let configOptions = {};
    pagers.map((p) => {
        configOptions[p.id] = { ...p };
    });
    return function (event) {
        console.log('pagerController event: ', event, configOptions);
        if (event.name == "pageUpdated") {
            let pageUpdated = configOptions[event.id]['events']['pageUpdated'];
            if (pageUpdated && typeof pageUpdated == "function") { // if it exists,
                console.log('pageUpdated USER_DEFINED: ', pageUpdated, this, event.id, configOptions[event.id], this[event.id]);
                configOptions[event.id]['events']['pageUpdated'](event.recordsPage, this[event.id], this); // execute user defined callback
            }
            else if (pageUpdated && typeof pageUpdated == "string") {
                console.log('pageUpdated STRING: ', pageUpdated, this, event.id, configOptions[event.id], this[event.id]);
                this[configOptions[event.id]['events']['pageUpdated']](event.recordsPage); // call pageUpdate defined in methods on vm
            }
            else {
                console.log('pageUpdated DEFAULT: ', event.id);
                this[event.id].recordsPage = event.recordsPage;
            }
        }
        if (event.name == "pageArgsChanged") {
            if (typeof (configOptions[event.id]) == 'undefined' || !configOptions[event.id]['events']['pageArgsChanged']) {
                console.error('pageArgsChanged event not defined on controller.');
                return;
            }
            console.log('executing pageArgsChanged callback...');
            let records = this[configOptions[event.id]['events']['pageArgsChanged']]({ pageNum: event.pageNum, pageSize: event.pageSize, id: event.id });
            console.log('CONTROLLER: records', records, typeof (records.then));
            if (typeof (records.then) == 'function') {
                records.then(records => this[event.id].records = records);
                return;
            }
            this[event.id].records = records;
        }
        if (event.name == "created") {
            console.log('Pager ' + event.id + ' created!');
            let createdArgs = configOptions[event.id]['events']['loadOnCreated'];
            console.log('createdArgs: ', createdArgs);
            if (createdArgs) {
                console.log(configOptions[event.id]['events']['pageArgsChanged'], createdArgs);
                this[event.id]['initialPage'] = createdArgs.pageNum; // set initial page
                let records = this[configOptions[event.id]['events']['pageArgsChanged']]({ pageNum: createdArgs.pageNum, pageSize: createdArgs.pageSize, id: configOptions[event.id] });
                if (typeof (records.then) == 'function') {
                    records.then(records => this[event.id].records = records);
                    return;
                }
                this[event.id].records = records;
            }
        }
    }
}

// depracated

// original controller
export const pagerController = function ({ id, recordsPage }) {
    console.log('pagerController');
    this[id].recordsPage = recordsPage;
}

