<template>
    <div class="container-fluid" v-show="bootstrapLoaded" style="display:none">
        <div class="row">
            <div class="col-lg-2"></div>
            <div class="col-lg-3 text-right">
                <h2>
                    vue-data-pager
                </h2>
            </div>
            <div class="col-lg-5 text-left" style="top:20px;">
                <ul class="nav nav-pills">
                    <li role="presentation" :class="currentView=='SimpleExample'?'active':''" @click="currentView='SimpleExample'"><a href="#">Simple Example</a></li>
                    <li role="presentation" :class="currentView=='MoreOptions'?'active':''" @click="currentView='MoreOptions'"><a href="#">More Options</a></li>
                    <li role="presentation" :class="currentView=='BlockMode'?'active':''" @click="currentView='BlockModeWController'"><a href="#">Block Mode</a></li>
                    <li role="presentation" :class="currentView=='MultiPagers'?'active':''" @click="currentView='MultiPagers'"><a href="#">Multiple Pagers</a></li>
                    <!-- <li role="presentation"><a href="#">Messages</a></li> -->
                </ul>
            </div>
            <div class="col-lg-2"></div>
        </div>
        <hr>
        <div class="row">
            <!-- <div class="col-xs-12 col-sm-8 col-sm-offset-2 col-md-8 col-lg-10 col-md-offset-2"> -->
            <transition name="fade" mode="out-in">
                <component :is="currentView"></component>
                <!-- <multi-pagers></multi-pagers> -->
            </transition>
        </div>
    </div>
    </div>
</template>

<script>
    import SimpleExample from './components/SimpleExample.vue';
    import MoreOptions from './components/MoreOptions.vue';
    import BlockMode from './components/Demo_BlockMode.vue';
    import BlockModeWController from './components/Demo_BlockMode_wController.vue';
    import MultiPagers from './components/MultiPagers.vue';
    export default {
        components: {
            SimpleExample,
            MoreOptions,
            BlockMode,
            BlockModeWController,
            MultiPagers
        },
        data() {
            return {
                bootstrapLoaded: false,
                currentView: "SimpleExample",
                activePill: "SimpleExample"
            }
        },
        methods: {
        },
        mounted() {
            console.log('App component mounted!');
            setTimeout(() => this.bootstrapLoaded = true, 150);
        }
    }

</script>

<style>

</style>


