<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-lg-3"></div>
            <div class="col-lg-6">
                <!-- <br> -->
                <!-- <div>
                    <button class="btn btn-md btn-primary" @click="loadPageFromServer">
                        Load Page of 200 Contacts
                    </button>
                    <button :class="pager.records.length>0?'active':'disabled'" class="btn btn-md btn-primary" @click="pager.records=[]">
                        Clear Contacts
                    </button>
                    <button class="btn btn-md btn-primary" @click="loadContacts({pageNum:3,id:'pager'})">
                        Load 3rd Page of 200 Contacts
                    </button>
                </div> -->
                <div class="space-down">
                    <h3></h3>
                    <button :class="sizes=='1'?'disabled':''" class="btn btn-md btn-primary" @click="changePageSizeOptions('1')">
                        <span style="color:lightgray">page sizes: <span style="font-weight: bold;color:white">5</span>, 10, 25, 50 items</span>
                    </button>
                    <button :class="sizes=='2'?'disabled':''" class="btn btn-md btn-primary" @click="changePageSizeOptions('2')">
                        <span style="color:lightgray">page sizes: 3, <span style="font-weight: bold;color:white">8</span>, 20 items</span>
                    </button>
                    <button :class="sizes=='3'?'disabled':''" class="btn btn-md btn-primary" @click="changePageSizeOptions('3')">
                        page sizes: 5 items only
                    </button>
                </div>
                <!-- <br><br> -->
                <button class="btn btn-md btn-primary space-down" @click="toggleStyle">
                    switch to {{ pager.pagerStyle.use == 'semantic' ? 'Bootstrap' : 'Semantic UI' }}
                </button>
                <img v-if="showSpinner" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <div class="space-down">
                    <data-pager v-bind="pager" @controller="pagerController"></data-pager>
                    <contact-list :contacts="pager.recordsPage"></contact-list>
                </div>
            </div>
            <div class="col-lg-3"></div>
        </div>
    </div>
</template>

<script>
    import ContactList from './contacts/ContactList.vue';
    import DataPager, { createPager, createController } from './data-pager/';
    let pager = createPager({
        // mode: "std", // leaving out mode will default to standard mode
        totalRecords: 200, // tell pager to load 1st page of 200 records, will use pageSize as totalRecords
        pagerStyle: { use: 'bootstrap' },
        events: {
            pageArgsChanged: 'loadContacts',
            loadOnCreated: { pageNum: 3, pageSize: 5 }
            // pageUpdated(page, pager, vm) {
            //     console.log('USER_DEFINED pageUpdated', pager, this);
            //     pager.recordsPage = page;
            // }
        }
    });
    let pagerController = createController(pager);

    export default {
        components: {
            ContactList,
            DataPager
        },
        data() {
            return {
                pager,
                showSpinner: false,
                sizes: '1'
            }
        },
        computed: {
            style() { return this.pager.pagerStyle.use }
        },
        methods: {
            toggleStyle() {
                if (this.pager.pagerStyle.use == 'bootstrap') {
                    this.pager.pagerStyle = { use: 'semantic' }
                    return;
                }
                this.pager.pagerStyle = { use: 'bootstrap' }
            },
            changePageSizeOptions(option) {
                console.log('changePageSizeOptions: ', option)
                let sizes = {
                    '1': undefined,
                    '2': [
                        { size: 3, text: "3 items", selected: false },
                        { size: 8, text: "8 items", selected: true },
                        { size: 20, text: "20 items", selected: false }
                    ],
                    '3': [
                        { size: 5, text: "5 items", selected: true }
                    ]
                }
                this.sizes = option;
                this.pager.pageSizes = sizes[option];
            },
            pagerController,
            // pageUpdated(page) {
            //     console.log('call pageUpdate defined in methods on vm', page);
            //     this.pager.recordsPage = page;
            // },
            loadPageFromServer() {
                this.pager.set({
                    // mode: "std", // leaving out mode will default to standard mode
                    totalRecords: 200, // tell pager to load 1st page of 200 records, will use pageSize as totalRecords
                });
                let firstPage = this.loadContacts({ pageNum: 1, pageSize: 5, id: "pager" }); // load initial page
                firstPage.then(r => {
                    pager.records = r;
                });
            },
            loadContacts({ pageNum = 1, pageSize = 5, id }) { // will receive pageArgs from pager's pageUpdated method if totalRecords > 0
                console.log(`loadContacts(${pageNum},${pageSize},id:${id})`);
                this.showSpinner = true;
                let vm = this;
                // return a "thenable" object such as a promise or deferred
                return $.getJSON('https://randomuser.me/api/?seed=foobar&page=' + pageNum + '&results=' + pageSize)
                    .then(res => {
                        // if you want to modify the data returned from the server before display, you could do it here!

                        // simply add row number to each contact
                        let records = res.results.map((c, i) => {
                            c.row = ((pageNum - 1) * pageSize) + i;
                            return c;
                        });
                        // show spinner for at least 300ms
                        setTimeout(() => vm.showSpinner = false, 300);
                        return records; // important to return the record set here if you add a "then" callback to your initial ajax request.
                    });
            }
        }
    }

</script>

<style>
    .space-down {
        margin-top: 6px;
    }
</style>


