<template>
    <!-- Contacts Display Area -->
    <div class="container">
        <div class="row">
            <div class="col-md-4 col-sm-6 ">
                <h2>Contact List</h2>
                <div v-if="contacts.length==0">(no contacts to show)</div>
                <ul class="list-group contact-list">
                    <li class="list-group-item hover" v-for="(c,i) in contacts" :key="i" @click="onContactClick(i)" style="cursor:pointer;">
                        {{`${c.row+1}. ${c.name.first} ${c.name.last}`}}
                        <img :src="c.picture.thumbnail" style="position:absolute;top:4px;right:20px;border-radius:3px;height:32px;width:auto;border:1px solid white">
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
            };
        },
        props: [
            'contacts'
        ],
        methods: {
            onContactClick(index) {
                console.log('contact clicked, id: ' + index);
                alert('you clicked on ' + this.contacts[index].name.first);
                this.$emit('contactClicked', this.contacts[index]); // pass the selected contact back up to parent (Home)
            }
        }
    }

</script>

<style>
    li.hover:hover {
        background-color: darkgray;
        color: white;
        font-weight: bold;
    }
</style>


