<template>
    <div class="container-fluid">
        <div class="row">
            <div class="col-lg-3"></div>
            <div class="col-lg-6">
                <br>
                <button :class="pager.records.length==0?'':'disabled'" class="btn btn-md btn-primary" @click="loadContacts">
                    Load Page of 200 Contacts
                </button>
                <button :class="pager.records.length>0?'':'disabled'" class="btn btn-md btn-primary" @click="pager.records=[]">
                    Clear Contacts
                </button>
                <img v-if="showSpinner" src="../images/cloudspinner_infinite.gif" style="height: 36px;width:auto;position: absolute;margin-left: 6px;">
                <data-pager v-bind="pager" @pageUpdated="updateContacts" @pageArgsChanged="loadContacts"></data-pager>
                <contact-list :contacts="pager.recordsPage"></contact-list>
                <!-- <contact-list :contacts="contacts"></contact-list> -->
            </div>
            <div class="col-lg-3"></div>
        </div>
    </div>
</template>

<script>
    import ContactList from './contacts/ContactList.vue';
    import DataPager, { createPager } from './data-pager/';
    let pager = createPager();
    export default {
        components: {
            ContactList,
            DataPager
        },
        data() {
            return {
                pager,
                contacts:[],
                showSpinner: false
            }
        },
        methods: {
            loadContacts({ pageNum = 1, pageSize = 5 }) { // will receive pageArgs from pager's pageUpdated method
                this.showSpinner = true;
                let vm = this; // due to the way Vue.js assigns the value of 'this', we must capture a reference to the Vue instance here.
                $.getJSON('https://randomuser.me/api/?seed=foobar&page=' + pageNum + '&results=' + pageSize)
                    .then(res => {
                        let records = res.results.map((c, i) => { // (optional) simply add row number to each contact
                            c.row = ((pageNum - 1) * pageSize) + i;
                            return c;
                        });
                        setTimeout(() => vm.showSpinner = false, 300); // show spinner for at least 300ms

                        vm.pager.set({ // update pager with new data
                            records,
                            totalRecords: 200 // this fixed number here could be a query total returned by data api
                        })
                    })
            },
            updateContacts(page) {
                // here we could do some additional mapping to page before making avail. to data view component
                this.pager.recordsPage = page;
                // this.contacts = page;
            }
        }
    }

</script>


