<!-- Vue.js Record Pager -->
<template>
    <div style="user-select: none;" class="record-pager">
        <!-- <div v-if="debug">
            <hr> numRecords: {{numRecords}}, pageSize: {{_pageSize}}, pageCount: {{_pageCount}}, mode: {{mode == "block" ? mode
            :"std"}}, totalRecords: {{totalRecords}},
            <br> selectedPageSize: {{selectedPageSize}}, currentPage: {{currentPage}},
            <br> pageSizes: {{JSON.stringify(pageSizes)}},
        </div> -->
        <!-- <transition name="fade" mode="out-in"> -->
        <div v-if="_pageCount>0 && records.length>0" style="position: relative;white-space: nowrap;">
            <div v-if="pageSizes.length==1 && pagerStyle.use=='semantic'" style="display: block;height: 20px;"></div>
            <select v-show="pageSizes.length>1" v-model="selectedPageSize" :class="_styles['size-selector']">
                <option 
                    v-for="option in pageSizes" 
                    :value="option.size"                         
                    :key="option.size">{{option.text}}
                </option>
            </select>
            <!-- <paginate :click-handler="pageChanged" ref="pager" :page-count="_pageCount" :margin-pages="1" :page-range="2" :initial-page="initialPage-1"
                :container-class="'pagination'" :page-class="'page-item'" :page-link-class="'page-link-item'" :prev-class="'prev-item'"
                :prev-link-class="'prev-link-item'" :next-class="'next-item'" :next-link-class="'next-link-item'">
            </paginate> -->
            <paginate v-bind="_styles" :click-handler="pageChanged" ref="pager" :page-count="_pageCount" :margin-pages="1" :page-range="2"
                :initial-page="initialPage-1">
            </paginate>
            <!-- v-bind="pagerStyle" -->
        </div>
        <!-- </transition> -->
    </div>
</template>

<script>
    import paginate from 'vuejs-paginate';
    export default {
        components: {
            paginate
        },
        props: {
            // debug: {
            //     default: false
            // },
            initialPage: {
                default: 1,
                type: Number
            },
            pageCount: {
                default: "0",
                type: String
            },
            pageSizes: {
                default: () => [
                    { size: 5, text: "5 items", selected: true },
                    { size: 10, text: "10 items" },
                    { size: 25, text: "25 items" },
                    { size: 50, text: "50 items" }
                ],
                type: Array
            },
            records: {
                default: () => [],
                type: Array
            },
            totalRecords: {
                default: 0,
                type: Number
            },
            pagerStyle: {
                default: () => ({
                    use: "bootstrap"
                }),
                type: Object
            },
            mode: {
                default: null, // standard mode
                type: String
            },
            id: {
                default: "pager",
                type: String
            }
        },
        data() {
            return {
                // _pageCount: 0,
                currentPage: 1,
                selectedPageSize: 5,
                forcePage: 0,
            }
        },
        methods: {
            selectPage(page) {
                console.log('selectPage, page:', page);
                if (this.$refs.pager && page > 0 && page <= this._pageCount) { // if pager is rendered
                    this.$refs.pager.selected = page - 1;
                    this.currentPage = page;
                }
            },
            pageChanged(page) {
                // console.log('pageChanged: ' + page);
                this.currentPage = page;
            },
            // selectedChanged(e) {
            //     console.log('selectedChanged: ', e.target.value);
            //     this.selectedPageSize = e.target.value;
            //     this.$refs.pager.selected = 0;
            //     this.currentPage = 1;
            // },

            // updatePageSizes() {
            //     let option = this._pageSizes.find(option => option.selected);
            //     console.log("updatePageSizes _pageSizes option:", option);
            //     this.selectedPageSize = option ? option.size : this._pageSizes[0].size;
            // },
            updatePageSizes() {
                let option = this.pageSizes.find(opt => opt.selected);
                console.log("updatePageSizes pageSizes option:", option);
                this.selectedPageSize = option ? option.size : this.pageSizes[0].size;
            },
            updateData(records) {
                console.log('updateData');
                // bad: tightly couples component to parent
                // if (this.$parent.pagerController) {
                //     this.$parent.pagerController({ id: this.id, recordsPage: this.recordsPage });
                //     return;
                // }
                this.$emit('controller', { // event args
                    name: 'pageUpdated',
                    id: this.id,
                    recordsPage: this.recordsPage
                }); // also emit event for those wanting to wire up manually
                this.$emit('pageUpdated', this.recordsPage); // also emit event for those wanting to wire up manually
            }
        },
        computed: {
            _styles() {
                if (this.pagerStyle.use == "bootstrap") {
                    return {
                        "container-class": "pagination",
                        "page-class": "page-item",
                        "page-link-class": "page-link-item",
                        "prev-class": "prev-item",
                        "prev-link-class": "prev-link-item",
                        "next-class": "next-item",
                        "next-link-class": "next-link-item",
                        "size-selector": "form-control-static bootstrap-size-selector"
                    }
                }
                if (this.pagerStyle.use == "semantic") {
                    return {
                        "container-class": "ui pagination menu",
                        // "page-class": "item",
                        "page-link-class": "item",
                        // "prev-class": "item",
                        "prev-link-class": "item",
                        "next-link-class": "item",
                        // "next-class": "item",
                        "no-li-surround": true,
                        "size-selector": "ui pagination semantic-size-selector"
                    }
                }
            },
            _pageCount() {
                // let count = this.pageCount; // does this mutate prop directly?
                let count;
                if (this._pageSize > 0) {
                    count = Math.ceil(this.numRecords / this._pageSize);
                }
                console.log('pageCount', count);
                // console.log('numRecords', this.numRecords);
                return count || this.pageCount;
            },
            _pageSize() {
                console.log('_pageSize', this.selectedPageSize)
                return parseInt(this.selectedPageSize);
            },
            // _pageSizes() {
            //     console.log("computed _pageSizes changed");
            //     let _default = [
            //         { size: 5, text: "5 items", selected: true },
            //         { size: 10, text: "10 items" },
            //         { size: 25, text: "25 items" },
            //         { size: 50, text: "50 items" }
            //     ];
            //     return this.pageSizes || _default;
            // },
            recordsPage() {
                let page;
                let start = (this.currentPage - 1) * this._pageSize;
                let end = start + this._pageSize;
                if (this.mode == "block") {
                    page = this.records.slice(start, end);
                    // console.log('RecordsPage', start, end, page);
                }
                else {
                    // console.log('RecordsPage: totalRecords !=0')
                    page = this.records.slice(0, this._pageSize);
                }
                return page;
            },
            numRecords() {
                // return this.totalRecords || this.records.length;
                return this.mode == "block" ? this.records.length : this.totalRecords;
            },
            // computed_selectedPageSize: function (newValue) {
            //     console.log('COMPUTED: selectedPageSize', newValue);
            //     if (this.$refs.pager) { // if pager is rendered
            //         this.$refs.pager.selected = 0;
            //     }
            //     this.currentPage = 1;
            //     if (this.mode == "block") {
            //         this.updateData(this.recordsPage);
            //         return;
            //     }
            //     this.$emit('pageArgsChanged', { pageNum: this.currentPage, pageSize: newValue, id: this.id });
            //     this.$emit('controller', { // event args
            //         name: 'pageArgsChanged',
            //         id: this.id,
            //         pageNum: this.currentPage,
            //         pageSize: newValue
            //     });
            // },
        },
        watch: {
            test(newVal, oldVal) { console.log('TEST', newVal, oldVal) },
            currentPage: function (newPageNum) {
                console.log('watch: currentPage', newPageNum, this._pageCount);
                if (this.recordsPage.length == 0) {
                    console.log('watch: recordsPage.length == 0');
                    this.currentPage = this.initialPage;
                    return;
                }
                if (this.mode == "block") {
                    console.log('watch: currentPage NO pageArgsChanged');
                    this.updateData(this.recordsPage);
                    return;
                }
                console.log('watch: currentPage pageArgsChanged');
                // check if last page and remaining # of records less than pageSize
                let size = this._pageSize;
                if (newPageNum == this._pageCount && this.totalRecords % size != 0) {
                    size = this.totalRecords % size;
                }
                // fire pageArgsChanged events
                this.$emit('pageArgsChanged', { pageNum: newPageNum, pageSize: size, id: this.id });
                this.$emit('controller', {
                    name: 'pageArgsChanged',
                    id: this.id,
                    pageNum: newPageNum,
                    pageSize: this._pageSize
                });
            },

            selectedPageSize: function (newValue) {
                console.log('watch: selectedPageSize', newValue);
                if (this.$refs.pager) { // if pager is rendered
                    this.$refs.pager.selected = 0;
                }
                this.currentPage = 1;
                if (this.mode == "block") {
                    this.updateData(this.recordsPage);
                    return;
                }
                if (this.records.length > 0) { // this check necessary to prevent pageArgsChanged from firing when pageSizes passed in as props
                    this.$emit('pageArgsChanged', { pageNum: this.currentPage, pageSize: newValue, id: this.id });
                    this.$emit('controller', { // event args
                        name: 'pageArgsChanged',
                        id: this.id,
                        pageNum: this.currentPage,
                        pageSize: newValue
                    });
                }
            },
            recordsPage: function () {
                console.log('watch: recordsPage');
                this.updateData(this.recordsPage);
            },
            pageSizes: function (newVal, old) {
                console.log("watch: _pageSizes changed", newVal, old);
                this.updatePageSizes();
            }
        },
        created() {
            // console.log('RecordPager created, props:', this.$props);
            this.updatePageSizes();
            this.$emit('controller', { name: "created", id: this.id });
            global.dp = this;
        }
    }

</script>

<style>
    .bootstrap-size-selector {
        user-select: none;
        position: relative;
        top: -32px;
        color: #337ab7;
        border-radius: 4px;
        border-color: #ddd;
    }

    .semantic-size-selector {
        user-select: none;
        position: relative;
        top: 1px;
        font-size: .90em;
        /* color: #337ab7; */
        height: 31px;
        border-radius: 4px;
        border-color: #ddd;
    }
</style>


